const {ccclass, property, requireComponent} = cc._decorator;

@ccclass
@requireComponent(cc.Button)
export default class DropdownItem extends cc.Component {
    @property(cc.Node)
    checkMark: cc.Node = null;

    @property(cc.Label)
    label: cc.Label = null;

    idx: number = -1;
}
